import Profile from './../assets/profile.jpg';
import {useState,useEffect} from 'react';
import $ from 'jquery';
import Typed from 'react-typed';
export default function Header(){
    const[services,setServices]=useState([]);
    useEffect(()=>{
        const services=$('.text-slider-items').text().split(',');
        setServices(services);
    },[]);
    return(
        <header>
            <div className="container-fluid" id="header">
                <div className="row header" style={{backgroundColor:"#0bceaf"}}>
                    <div className="col-sm-6">
                        <div className="p-3 text-center">
                            <img src={Profile} className="profile-img" alt="profile"/>
                        </div>
                    </div>
                    <div className="col-sm-6">
                        <div className="p-3 centered-content">
                            <p className="welcome-text">Hello, I'm</p>
                            <h2>Pankaj Saini</h2>
                            <h5 className="text-slider-items">Web Developer, Web Designer, Full Stack Web Developer</h5>
                            <h5 className="text-slider text-light">
                                <Typed strings={services} startDelay={300} typeSpeed={100} backSpeed={100} backDelay= {100} smartBackspace= {true} loop= {true} showCursor= {true} cursorChar= {"|"}/>
                            </h5>
                        </div>
                    </div>
                    
                </div>
            </div>
        </header>
    )
}