import {Link} from 'react-router-dom';
import {HashLink} from 'react-router-hash-link'
export default function Nav(){
    const scrollWithOffset = (el) => {
        const yCoordinate = el.getBoundingClientRect().top + window.pageYOffset;
        const yOffset = -80; 
        window.scrollTo({ top: yCoordinate + yOffset, behavior: 'smooth' }); 
    }
    return(
        <nav className="navbar navbar-expand-lg sticky-top navbar-light bg-light">
            <Link className="navbar-brand" to="#">PortFolio</Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle Navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto">
                    <li className="nav-item active">
                        <Link to="" className="nav-link">Home</Link>
                    </li>
                    <li className="nav-item active">
                        <HashLink smooth to="#about" className="nav-link" scroll={el => scrollWithOffset(el)}>About</HashLink>
                    </li>
                    <li className="nav-item active">
                        <HashLink smooth to="#work" className="nav-link" scroll={el => scrollWithOffset(el)}>Work</HashLink>
                    </li>
                    <li className="nav-item active">
                        <HashLink smooth to="#contact" className="nav-link" scroll={el => scrollWithOffset(el)}>Contact</HashLink>
                    </li>
                    <li className="nav-item active">
                        <Link to="/blog" className="nav-link">Blogs</Link>
                    </li>
                </ul>
            </div>
        </nav>
    );
}