import $ from 'jquery';
export default function Contact(){
    const isValidData=(data)=>{
        const {name,email,subject,message}=data;
        const errors={exist:false,name:{valid:true,message:""},email:{valid:true,message:""},subject:{valid:true,message:""},message:{valid:true,message:""}};
        if(name===""){
            errors.exist=true
            errors.name={valid:false,message:"Name is required"};
        }
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if(email===""){
            errors.exist=true;
            errors.email={valid:false,message:"Please Enter Email"}
        }
        if(re.test(String(email).toLowerCase())){
            errors.exist=false;
            errors.email={valid:true,message:""};
        }
        else{
            errors.exist=true;
            errors.email={valid:false,message:"Invalid Email"}
        }
        if(subject===""){
            errors.exist=true;
            errors.subject={valid:false,message:"Subject is empty"};
        }
        if(message===""){
            errors.exist=true;
            errors.message={valid:false,message:"Message is empty"};
        }
        return errors;
    }
    const sendMail=async(evt)=>{
        evt.preventDefault();
        $('.msg').text("");
        $('.name').text("");
        $('.email').text("");
        $('.subject').text("");
        $('.message').text("");
        const name=$('#name').val().trim(),email=$('#email').val().trim(),subject=$('#subject').val().trim(),message=$('#message').val().trim();
        const isValid=isValidData({name,email,subject,message});
        if(!isValid.exist){
            try{
                const response=await fetch('/',{method:"POST",headers:{'Content-Type':'application/json'},
                    body:JSON.stringify({
                        name,email,subject,message
                    })
                });
                const data=await response.json();
                if(response.ok){
                    $('.msg').text(data.message);
                }else{
                    $('.msg').text(data.message);
                    console.log(data.errors);
                }
            }catch(err){
                $('.msg').text(err);
                console.log('error occured',err);
                console.log(err);
            }
        }else{
            $('.name').text(isValid.name.message);
            $('.email').text(isValid.email.message);
            $('.subject').text(isValid.subject.message);
            $('.message').text(isValid.message.message);
        }
    }
    return (
        <>
        <div className="container-fluid pt-3 pb-3" id="contact">
            <div className="row">
                <div className="col-sm-6 mx-auto">
                    <div className="contact-form">
                        <h4 className="text-center">Contact</h4>
                        <form onSubmit={sendMail}>
                            <div className="feedback text-center">
                                <p className="msg"></p>
                            </div>
                            <div className="form-group">
                                <input className="form-control" id="name" type="text" placeholder="Enter Name"/>
                                <span className="name text-danger"></span>
                            </div>
                            <div className="form-group">
                                <input className="form-control" id="email" type="email" placeholder="Enter Email"/>
                                <span className="email text-danger"></span>
                            </div>
                            <div className="form-group">
                                <input className="form-control" id="subject" type="text" placeholder="Enter Subject"/>
                                <span className="subject text-danger"></span>
                            </div>
                            <div className="form-group">
                                <textarea className="form-control" id="message" rows="4" placeholder="Type Message..."/>
                                <span className="message text-danger"></span>
                            </div>
                            <div className="form-group">
                                <button className="btn btn-primary" type="button" onClick={sendMail}>Send</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
        <footer className="" id="footer">
		<div className="container-fluid">
			<div className="row">
				<div className="col-md-4 mx-auto text-center">
					<p>© 2021 Copyright: All rights reserved.</p>
				</div>
			</div>
			<div className="row">
				<div className="col-md-6 mx-auto text-center">
					<ul className="list">
                    <li className="list-item">
							<a href="mailto:sainipankaj1708@gmail.com"><span className="fa fa-envelope"></span></a>
						</li>
						<li className="list-item">
							<a href="https://github.com/codemoretolearnmore"><span className="fa fa-github"></span></a>
						</li>
						<li className="list-item">
							<a href="https://www.linkedin.com/in/pankaj-saini-65598717a"><span className="fa fa-linkedin"></span></a>
						</li>
						<li className="list-item">
							<a href="https://www.instagram.com/spankaj91/"><span className="fa fa-instagram"></span></a>
						</li>
						<li className="list-item">
							<a href="https://www.facebook.com/profile.php?id=100032019511835"><span className="fa fa-facebook"></span></a>
						</li>
					</ul>
				</div>
			</div>
			<div className="row">
				<div className="col-md-4 mx-auto text-center">
					<p>Designed By Pankaj Saini with <span className="fa fa-heart" style={{color:"red"}}></span></p>
				</div>
			</div>
		</div>
	</footer>
        </>
    );
}